import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from '../sevices/api.service';
import { JwtHelperService } from '@auth0/angular-jwt';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  roles:Array<any>;
  username:string;
  password:string;
  showIndicator:boolean;
  erreurMessage:string;


  
  constructor(private formBuilder: FormBuilder, private router: Router,private apiService: ApiService) { }

  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      username: ['', Validators.compose([Validators.required])],
      password: ['', Validators.required]
  })

}
//1
onSubmit() {

  const body ={
    "username":this.loginForm.controls.username.value,
    "password":this.loginForm.controls.password.value
   }

   
    this.apiService.login(body).subscribe(
      (data) => {
        localStorage.setItem('token', data.body.Authorization);
        let helper = new JwtHelperService();
        let decodedToken = helper.decodeToken( localStorage.getItem('token'));
        this.roles=decodedToken.roles;
        this.router.navigate(['pages']);
       },
     (error) => {
      alert(error.error.error_description)
  });
}
//2
login(){
  const body ={
    "username":this.username,
    "password":this.password
   }
   
   if(this.username && this.password){
     this.showIndicator=true;
    this.apiService.login(body).subscribe(
      (data) => {
       localStorage.setItem('token', data.body.Authorization);
        let helper = new JwtHelperService();
        let decodedToken = helper.decodeToken( localStorage.getItem('token'));
        this.roles=decodedToken.roles;
        this.router.navigate(['pages']);

      },
      (error) => {

        this.showIndicator=false;
        this.erreurMessage=error.error.message;
        console.log(error);
      
    });
  }
  else{
     this.erreurMessage='username end password required';
    }
      }
isAdmin(){
      
  for(let r of this.roles ){
     if(r.authority=='admin')
     { return true 
       }
      }
  return false;

  }


}